* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: #1a1a1a;
  text-decoration: none;
}

ul, li {
  list-style: none;
}

html {
  color: #7e7e7e;
  scroll-behavior: smooth;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

body {
  width: 100vw;
  cursor: none;
  transition: .3;
  background-color: #fff;
  flex-flow: wrap;
  display: flex;
  position: absolute;
  overflow-x: hidden;
}

main {
  width: 80%;
  min-height: 100vh;
  margin-left: 20%;
}

@media screen and (max-width: 1279px) {
  main {
    width: 100%;
    margin: 0;
  }
}

.blur {
  filter: blur(3px);
}

::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  background: #1a1a1a;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #3b3b3b;
}

::-webkit-scrollbar-track {
  background: #f0f0f0;
  border-radius: 0;
  box-shadow: inset 0 0 #f0f0f0;
}

::selection {
  text-shadow: none;
  background: #b3d4fc;
}

hr {
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
  display: block;
}

input {
  background-color: #0000;
  border: none;
  outline: none;
}

audio, canvas, iframe, img, svg, video {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.hidden, [hidden] {
  display: none !important;
}

.sr-only {
  clip: rect(0, 0, 0, 0);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.sr-only.focusable:active, .sr-only.focusable:focus {
  clip: auto;
  height: auto;
  white-space: inherit;
  width: auto;
  margin: 0;
  position: static;
  overflow: visible;
}

.invisible {
  visibility: hidden;
}

.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

@media print {
  *, :before, :after {
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
    background: #fff !important;
  }

  a, a:visited {
    text-decoration: none;
  }

  a[href]:after {
    content: " (" attr(href) ")";
  }

  abbr[title]:after {
    content: " (" attr(title) ")";
  }

  a[href^="#"]:after, a[href^="javascript:"]:after {
    content: "";
  }

  pre {
    white-space: pre-wrap !important;
  }

  pre, blockquote {
    page-break-inside: avoid;
    border: 1px solid #999;
  }

  thead {
    display: table-header-group;
  }

  tr, img {
    page-break-inside: avoid;
  }

  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }

  h2, h3 {
    page-break-after: avoid;
  }
}

button {
  background-color: #0000;
  border: none;
  outline: none;
}

